.wrapperDiv {
    width: 70%;
    margin: auto;
    display: grid;
    grid-template-columns: 1fr;
    grid-column-gap: 20px;
    grid-template-areas: 
    "img img img"
    "left main right";
}

.mainDiv {
    width: 100%;
    margin: auto;
    grid-area: main;
    padding-bottom: var(--welcome-page-grid-gap);
}
.smallText {
    font-size: 13px;
}
.smallText span {
    text-decoration: underline;
    cursor: pointer;
}
.postsWrapper {
    margin-top: var(--welcome-page-grid-gap);
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-column-gap: var(--welcome-page-grid-gap);
    grid-row-gap: var(--welcome-page-grid-gap);
}


